/* General Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
}

/* Main Container */
.container {
  display: flex;
  height: 100vh; /* Full screen height */
  /* align-items: center;
  justify-content: center; */
}
header{
  margin-top: 0px;
}
/* Left Image Section */
.image-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Full height */
  background-color: #1e1e1e; /* Optional background for contrast */
}

.profile-image {
  width: 100%; /* Adjust size */
  /* max-width: 500px; */
  height: 100vh;
  border-top-left-radius: 30%;
  /* border-bottom-left-radius: 50%; */
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  /* border-radius: 50%; Makes it circular */
  object-fit: cover;
  box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.1);
}

/* Right Content Section */
.content {
  
  flex: 1;
  padding: 40px;
  text-align: center;
}
#typing{color: aliceblue;
font-size: 60px;}
/* .welcome{background-color: blue;
padding: 0.1px;
margin-top: 0px;} */
p{font-size: 25px;}
#spotlight{font-size: 20px;
text-decoration: none;
color: blueviolet;

}
button{border-color: blue;
padding: 8px;
background-color: #1e1e1e;
color: aliceblue;
border-radius: 5px;}
.spot{display: flex;
justify-content: center;
gap: 50px;
margin: 30px;
}
button:hover{background-color: rgba(10, 10, 173, 0.897);
border-color: aliceblue;}
#spotlight:hover{color: blue;}
.contact-links{display: flex;
justify-content: space-around;
color: aliceblue;
background-color: aliceblue;
padding: 5px;
margin: 0px;
font-size: 18px;
font-weight: bold;
}
.contact{text-decoration: none;
color: blueviolet;}
.contact:hover{color: blue;}
.highlight{color: burlywood ;}
.icon:hover{ transform: scale(1.1);
  font-size: 20px;
  
}
.icon{text-decoration: none;
}
.contact-links span{ transform: translateY(-5px); 
  display: inline-block;}
  /* Chatbot button */
.chatbot-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  width: 90px;
  height: 75px;
  font-size: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.chatbot-btn:hover {
  background-color: #0056b3;
}

/* Chatbot container */
.chatbot-box {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 300px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

/* Chatbot header */
.chat-header {
  background: #007bff;
  color: white;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header button {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
}

/* Chat messages */
.chat-body {
  height: 250px;
  overflow-y: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.user {
  align-self: flex-end;
  background: #007bff;
  color: white;
  padding: 8px 12px;
  border-radius: 15px;
  margin: 5px;
}

.bot {
  align-self: flex-start;
  background: #e9e9e9;
  color: black;
  padding: 8px 12px;
  border-radius: 15px;
  margin: 5px;
}

/* Chat input */
.chat-input {
  display: flex;
  padding: 10px;
  background: white;
  border-top: 1px solid #ddd;
}

.chat-input input {
  flex: 1;
  border: none;
  padding: 8px;
  outline: none;
}

.chat-input button {
  border: none;
  background: #007bff;
  color: white;
  padding: 8px 12px;
  cursor: pointer;
}
.chatbot{margin-top: 0px;}